<template>
	<div class="container">
		<mt-header fixed title="征信网点" class="title">
		  <router-link to="/Index" slot="left">
		    <mt-button icon="back">返回</mt-button>
		  </router-link>
		</mt-header>
		<div class="header_bg">
			<div class="header">
				<p>—{{net_title}}—</p>
			</div>
		</div>
		<div class="main">
			<div class="main_list" v-for="item in main_list">
				<div class="titlebar" style="text-align: center;font-weight: bold;font-size: 18px;border-bottom: 1px solid #eee;padding-bottom: 5px;">{{item.title}}</div>
				<p>查询网点：{{item.name}}</p>
				<p>详细地址：{{item.address}}</p>
				<a :href="'tel:'+item.phone" style="text-decoration:none">
					<div class="phone">
						<p>联系电话：{{item.phone}}</p>
						<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/13.png"/>
					</div>
				</a>
				<p style="color:#50D186;margin-top:5px;" @click="go_map">打开地图▼</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return{
				net_title:"大泉州个人征信报告查询网点",
				main_list:[{title:'泉州市区',name:'中国人民银行泉州支行',address:'丰泽区丰泽街888号',phone:'18888899999'},
				{title:'泉州市区',name:'中国人民银行泉州支行',address:'丰泽区丰泽街888号',phone:'18888899999'},
				{title:'泉州市区',name:'中国人民银行泉州支行',address:'丰泽区丰泽街888号',phone:'18888899999'},
				{title:'泉州市区',name:'中国人民银行泉州支行',address:'丰泽区丰泽街888号',phone:'18888899999'}]
			}
		},
		methods:{
			go_map:function(){
				this.$router.push({
					path:'/gaode_map'
				})
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.container{
		width: 100%;
	}
	.title{
		background: #fff;
		color:#000;
		height: 40px;
	}
	.header_bg{
		
		background: #fff;
		margin-top:50px;
		padding:10px
	}
	.header{
		
		background:#F5F5F5;
		border-radius: 10px;
	}
	.header p{
		color:#4B90CC;
		text-align: center;
		padding:10px;
		font-size: 18px;
	}
	.main{
		
		width: 95%;
		margin:0 auto;
		margin-top:20px;
	}
	.main .main_list{
		padding:10px;
		margin-bottom:10px;
		margin-top:10px;
		background: #fff;
	}
	.main_list p{
		line-height: 27px;
	}
	.phone{
		color:#1296DB;
		display: flex;
	}
	.phone img{
		width: 30px;
		height: 30px;
		margin-left:20px
	}
</style>


