<template>
	<div class="container">
		<mt-header fixed title="登录" class="title">
		  <router-link to="/Index" slot="left">
		    <mt-button icon="back">返回</mt-button>
		  </router-link>
		</mt-header>
		<div class="header_bg">
			<div class="header">
				<div class="titlebar">
					<p>账号登录</p>
				</div>
				<div class="number">
					<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/21.png" alt="" />
					<input type="" name="" id="" value="" placeholder="请输入账号"/>
				</div>
				<div class="password">
					<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/14.png"/>
					<input type="password" name="" id="" value="" placeholder="请输入密码"/>
				</div>
				<div class="fit">
					<p>忘记密码</p>
					<p @click="go_register">没有账号？<span style="color:#26A2FF">注册</span></p>
				</div>
				<button>立即登录</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return{
				phone:16699988666
			}
		},
		methods:{
			go_register:function(){
				this.$router.push({
					path:'/register'
				})
			}
		}
	}
</script>

<style>
	.container{
		width: 100%;
	}
	.title{
		
	}
	.header_bg{
		
		background: #fff;
		width: 90%;
		margin:auto;
		margin-top:60px;
		border-radius: 5px;
	}
	.header {
		padding:15px;
	
	}
	.titlebar{
		text-align: center;
		padding-bottom: 10px;
		border-bottom: 1px solid #37B5FA;
	}
	.titlebar p{
		font-size: 20px;
		color:#26A2FF;
		font-weight: bolder;
	}
	.header img{
		width: 23px;
		height: 23px;
		position: absolute;
		left:10px;
		top:7px
	}
	.header input{
		margin:auto;
		width: 90%;
		padding:8px;
		border-radius: 5px;
		border:1px solid #ccc;
		font-size: 16px;
		text-align: right;
		
	}
	.number{
		width: 90%;
		margin:auto;
		margin-top:10px;
		text-align: center;
		position: relative;
	}
	.password{
		width: 90%;
		margin: auto;
		margin-top:15px;
		text-align: center;
		position: relative;
	}
	.header button{
		width: 85%;
		margin:auto;
		display: block;
		margin-top: 10px;
		font-size: 16px;
		padding:5px;
		color:#eee;
		background: #26A2FF;
		border:1px solid #fff;
		border-radius: 5px;
	}
	.fit{
		display: flex;
		justify-content: space-between;
		width: 85%;
		margin:auto;
		font-size: 14px;
		margin-top:11px
	}
</style>


