import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Create from 'pages/Create'
import Mange from 'pages/Mange'
import Index from 'pages/Index'
import my_daikuan from 'pages/my_daikuan'
import apply from 'pages/apply'
import business from 'pages/business'
import ziliao_fill from 'pages/ziliao_fill'
import net from 'pages/net'
import gaode_map from 'pages/gaode_map'
import jindu from 'pages/jindu'
import user_login from 'pages/user_login'
import register from 'pages/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'Index'
    },
    {
      path: '/',
      name: 'Create',
      component: Create,
      children: [
      	{path: '/Index', name: 'Index', component: Index},
      	{path: '/my_daikuan', name: 'my_daikuan', component: my_daikuan},
      	{path: '/apply', name: 'apply', component: apply},
      	{path: '/business', name: 'business', component: business},
      	{path: '/ziliao_fill', name: 'ziliao_fill', component: ziliao_fill},
      	{path: '/net', name: 'net', component: net},
      	{path: '/gaode_map', name: 'gaode_map', component: gaode_map},
      	{path: '/jindu', name: 'jindu', component: jindu},
      	{path: '/user_login', name: 'user_login', component: user_login},
      	{path: '/register', name: 'register', component: register}
      ]
    },
    {
      path: '/Mange',
      name: 'Mange',
      component: Mange,
      children: []
    }

  ]
})



