<template>
	<div class="container">
		<mt-header fixed title="地图" class="title">
			<router-link to="/net" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>

		<div class="amap-page-container" style="height:300px;">
	      <el-amap vid="amapDemo" :zoom="zoom" :center="center" class="amap-demo">
	        <el-amap-circle v-for="(circle,index) in circles" :key="index" :center="circle.center" :radius="circle.radius" :fill-opacity="circle.fillOpacity" :events="circle.events"></el-amap-circle>
	      </el-amap>
	    </div>
	</div>
</template>

<script>
	export default {
      data () {
        return {
          zoom: 15,
          center: [116.307629, 40.058359],
          circles: [
            {
              center: [116.307629, 40.058359],
              radius: 200,
              fillOpacity: 0.2,
              events: {
                click: () => {
                  alert('click');
                }
              }
            }
          ]
        }
      }
    };
</script>

<style>
	.amap-page-container {
      height: 200px;
    }
</style>


