<template>
	
	<div class="container">
		<div class="zhezhao" :style='"height:"+fullHeight+"px"' v-if="zhezhao"></div>
		<mt-header fiexd title="我要贷款" class="title">
		  <router-link to="/Index" slot="left">
		    <mt-button icon="back">返回</mt-button>
		  </router-link>
		</mt-header>
		<div class="banner" style="width: 100%;height: 200px;">
			<mt-swipe :auto="4000" :speed="1500">
			  <mt-swipe-item><img style="width: 100%;height: 100%" src="https://www.zgsj1.com//Public/Upload/images/moban/Housechan/uid_95/moban_id_4255/1529584733.gif"/></mt-swipe-item>
			  <mt-swipe-item><img style="width: 100%;height: 100%" src="https://www.zgsj1.com//Public/Upload/images/moban/Housechan/uid_95/moban_id_4255/1530611838.png"/></mt-swipe-item>
			  <mt-swipe-item><img style="width: 100%;height: 100%" src="https://www.zgsj1.com//Public/Upload/images/moban/Housechan/uid_95/moban_id_4255/1530611611.png"/></mt-swipe-item>
			</mt-swipe>
		</div>
		<div class="main">
			<div class="mainbar">
				<div class="box">
					<div class="name"><p>姓名</p></div>
					<div class="txt">
						<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/20.png" alt="" />
						<input type="text" placeholder="请填写姓名"/>
					</div>
				</div>
				<div class="box">
					<div class="name"><p>手机号</p></div>
					<div class="txt">
						<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/15.png" alt="" />
						<input type="number" placeholder="手机号" maxlength="11"/>
					</div>
				</div>
				<div class="box">
					<div class="name"><p>验证码</p></div>
					<div class="txt">
						<input type="text" style="width: 50%;"/>
						<button style="background: #CCCCCC;border-radius: 10px;padding: 5px;" @click="yanzheng" :disabled="dis">{{huoqu}}</button>
					</div>
				</div>
				<div class="box" @click="xuanze_region">
					<div class="name"><p>意向贷款地区</p></div>
					<div class="txt">
						<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/22.png" alt="" />
						<input type="text" disabled="" :value='region'/>
					</div>
				</div>
				<div class="box">
					<div class="name"><p>意向贷款金额</p></div>
					<div class="txt">
						<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/23.png" alt=""/>
						<input type="text" placeholder="请填写贷款金额"  style="width: 75%;"/>
						<p style="line-height: 33px;">万元</p>
					</div>
				</div>
				<div class="box" @click="xuanze_time">
					<div class="name"><p>方便联系时段</p></div>
					<div class="txt">
						<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/24.png" alt="" />
						<input type="text" disabled="" v-model="txt_time"/>
					</div>
				</div>
				<div class="box">
					<div class="name"><p>备注</p></div>
					<div class="txt">
						<img src="https://www.zgsj1.com///Public/fm_static/bg_img/daikuan/25.png" alt="" />
						<input type="text" placeholder="请填写其他说明" style="padding-left:35px"/>
						
					</div>
				</div>
				
				<div class="sub">
					<p>我要办理</p>
				</div>
			</div>
			
		</div>
		<div class="picker_region" v-if="picker_r" :style='"top:"+(fullHeight-220)+"px"'>
			<mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
			<p @click="close_region">关闭</p>
		</div>
		<div class="picker_time" v-if="picker_t" :style='"top:"+(fullHeight-220)+"px"'>
			<mt-picker :slots="time" @change="ontime"></mt-picker>
			<p @click="close_time">关闭</p>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return{
				region:'',
				picker_r:false,
				fullHeight:'',
				zhezhao:false,
				picker_t:false,
				txt_time:'',
				dis:false,
				huoqu:'获取验证码',
				slots: [
		        {
		          flex: 1,
		          values: ['厦门市'],
		          className: 'slot1',
		          textAlign: 'right'
		        }, {
		          divider: true,
		          content: '-',
		          className: 'slot2'
		        }, {
		          flex: 1,
		          values: ['思明区', '湖里区', '集美区', '海沧区', '同安区', '翔安区'],
		          className: 'slot3',
		          textAlign: 'left'
		        }
		      ],
		      time:[{values: ['全部','8:00-10:00','10:00-12:00','12:00-14:00','14:00-16:00','16:00-18:00']}],
		      currentTime:60,
		      interval:''
			}
		},
		mounted:function(){
//			获取屏幕高度
			console.log(document.documentElement.clientHeight)
			this.fullHeight=document.documentElement.clientHeight
			console.log(this.fullHeight,111)
		},
		methods:{
			onValuesChange:function(index,value){
				console.log(value)
				this.region=value
			},
			ontime:function(index,value){
				console.log(value)
				this.txt_time=value
			},
//			选择地区
			xuanze_region:function(){
				this.picker_r=true
				this.zhezhao=true
			},
//			关闭选择地区
			close_region:function(){
				this.picker_r=false
				this.zhezhao=false
			},
			/*选择时间*/
			xuanze_time:function(){
				this.picker_t=true
				this.zhezhao=true
			},
//			关闭选择时间
			close_time:function(){
				this.picker_t=false
				this.zhezhao=false
			},
//			获取验证码
			yanzheng:function(){
				var _this = this
				_this.dis=true
				var currentTime = _this.currentTime;
				clearInterval(_this.interval);
				_this.interval=setInterval(function(){
					currentTime--;
					_this.huoqu=currentTime+'秒'
					if(currentTime<=0){
						clearInterval(_this.interval);
						_this.huoqu='重新发送'
						_this.currentTime=60
						_this.dis=false
					}
				},1000)
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.container{
		width: 100%;
		position: relative;
	}
	.title{
		background: #fff;
		color:#000
	}
	.main{
		width: 100%;
		background: #fff;
	}
	.mainbar{
		padding:20px
	}
	.mainbar .box{
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 42px;
	}
	.box .name{
		width: 35%;
	}
	.box .txt{
		width: 65%;
		display: flex;
		justify-content: space-between;
		position: relative;
	}
	.txt img{
		position: absolute;
		width: 20px;
		height: 20px;
		left:7px;
		top:7px
	}
	.box input{
		height: 20px;
		width: 100%;
		border-radius: 5px;
		border:1px solid #ccc;
		padding:7px;
		text-align: right;
		
	}
	.sub{
		width: 70%;
		margin:30px auto;
		background-image: linear-gradient(to left, #E6E6E8,#F8F8F8,#E6E6E8);
		border-radius: 15px;
		
	}
	.sub p{
		text-align: center;
		padding:5px;
		
	}
	.picker_region{
		width: 80%;
		position: absolute;
		background: #fff;
		left: 50%;
		transform: translate(-50%);
		border-radius: 5px 5px 0 0;
		z-index: 2;
		
	}
	.picker_region p{
		text-align: center;
		font-size: 20px;
		border-top: 1px solid #ccc;
		padding:5px;
		color:#70CBFA
	}
	.zhezhao{
		background: #000000;
		width: 100%;
		height: 300px;
		position: fixed;
		z-index: 1;
		opacity: 0.8;
	}
	.picker_time{
		width: 80%;
		position: absolute;
		background: #fff;
		left: 50%;
		transform: translate(-50%);
		border-radius: 5px 5px 0 0;
		z-index: 2;
	}
	.picker_time p{
		text-align: center;
		font-size: 20px;
		border-top: 1px solid #ccc;
		padding:5px;
		color:#70CBFA
	}
</style>


