<template>
	<div class="container">
		<mt-header fixed title="进度" class="title">
		  <router-link to="/apply" slot="left">
		    <mt-button icon="back">返回</mt-button>
		  </router-link>
		</mt-header>
		<div class="list" style="margin-top: 40px;">
			<div class="listbar" v-for="item,index in my_list">
				<p>{{item.title}}</p>
				<span>{{item.zt}}</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data:function(){
			return{
				my_list:[{title:'提交申请',zt:'未提交'},{title:'平台接单',zt:'未分配'},{title:'银行受理',zt:'未处理'},
				{title:'审核进度',zt:'尚未审核'},{title:'抵押登记',zt:'未送件'},{title:'放款状态',zt:'未放款'}],
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.container{
		width: 100%;
	}
	.title{
		background: #fff;
		color:#000
	}
	.list{
		padding:10px;
		
	}
	.listbar{
		background: #fff;
		display: flex;
		justify-content: space-between;
		padding:14px;
		border-radius: 5px;
		margin-bottom: 15px;
		box-shadow: 1px 2px 3px 2px #D8D8D8;
	}
	.listbar p{
		font-weight: bold;
	}
	.listbar span{
		color:gray;
	}
</style>


